"use client";

import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import HomePage from '../components/HomePage';
import ProductPage from '../components/ProductPage';
import VendorArea from '../components/VendorArea';
import { mockProducts } from '../lib/mockData';

export default function App() {
  const [view, setView] = useState({ page: 'home', id: null });
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredProducts, setFilteredProducts] = useState(mockProducts);
  
  // Use an effect to filter products whenever the search term changes
  useEffect(() => {
    const handleSearch = () => {
      if (searchTerm.trim() === '') {
        setFilteredProducts(mockProducts);
      } else {
        const lowercasedQuery = searchTerm.toLowerCase();
        const results = mockProducts.filter(product =>
          product.name.toLowerCase().includes(lowercasedQuery) ||
          product.subtitle.toLowerCase().includes(lowercasedQuery) ||
          product.tags.some(tag => tag.toLowerCase().includes(lowercasedQuery)) ||
          product.vendor.toLowerCase().includes(lowercasedQuery) ||
          product.shortDescription.toLowerCase().includes(lowercasedQuery)
        );
        setFilteredProducts(results);
      }
      // Return to the homepage to show search results when user starts typing
      if (searchTerm.trim() !== '') {
        setView({ page: 'home', id: null });
      }
    };

    handleSearch();
  }, [searchTerm]); // Re-run this effect whenever searchTerm changes

  const renderView = () => {
    switch (view.page) {
      case 'product':
        const product = mockProducts.find(p => p.id === view.id);
        if (!product) {
            return <HomePage products={filteredProducts} setView={setView} />;
        }
        return <ProductPage product={product} setView={setView} />;
      case 'vendor':
        return <VendorArea setView={setView} />;
      case 'home':
      default:
        return <HomePage products={filteredProducts} setView={setView} />;
    }
  };

  return (
    <div className="bg-gray-50 min-h-screen font-sans">
        <Header 
          setView={setView} 
          searchTerm={searchTerm}
          setSearchTerm={setSearchTerm} 
        />
        {renderView()}
        <Footer />
    </div>
  );
}
